.products-section {
    width: 100%;
    min-height: calc(100vh - 50px);
    background: #171c21;
}
.max-products{
    width: 100%;
    min-height: calc(100vh - 50px);
    display: flex;
    align-items: center;
    flex-direction: column;
}
.max-products header {
    width: 90%;
    height: 95px;
    display: flex;
    align-items: center;
}
.max-products header h1 {
    color: #ffff;
    margin-right: 20px;
}
.style-products {
    width: 95%;
    min-height:80vh;
    border-radius: 5px;
    border: 1px solid #2f384b;
    display: grid;
    grid-template-columns: repeat(5 , 1fr);
}
.style-products > div {
    justify-self: center;
    margin-top: 15px;
}